<template>
  <div class="city">
    <my-header title="">
      <span slot="left">
            <img src="../../assets/img/back.png" @click="back">
      </span>  
    </my-header>
    <p></p>
    <p ref="city">{{cityname}}</p>
    <p class="txt1"></p>
		<ul v-for="(item,index) in city">
			<li @click="backFn(index)" ref="name">{{item}}</li>
		</ul>
  </div>
</template>

<script>
import Header from "../share/header.vue";
import {mapGetters,mapActions} from "vuex"
export default {
  data () {
    return {
    	cityname:'',
      city:['','','','','','','','','','','','','','','','','','','','','','','']
    }
  },
  methods:{
    ...mapActions([//mapActions
      "setCity"//actions
      ]),
  	backFn :function  (index) {
      this.$store.dispatch('setCity',this.city[index]),
      this.$refs.name[index].style.backgroundColor = 'rgba(213,213,213,.8)'
  		this.$router.push('/')
  	},
    back :function (){
      this.$router.push('/')
    }
  },
  components:{
    "my-header":Header
  },
mounted: function () {
    this.cityname = this.$store.state.city
 }
}
</script>


<style  lang='stylus' scoped>
@import '../../assets/common/stylus/common.styl'
.city
    padding-top pxtorem(100px)
    background-color white
    p
      font-size pxtorem(40px)
      height pxtorem(80px)
      margin-top pxtorem(20px)
      line-height pxtorem(80px)
      text-indent 0.5rem
    .txt1
      background-color rgba(213,213,213,.8)
    ul
      font-size pxtorem(40px)
      text-indent 0.5rem
      li
        border-top 1px solid #ccc
        height pxtorem(80px)
        line-height pxtorem(80px)
</style>



